* {
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}


body {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background-color: #F5F5F5;
  font-weight: 300;
}

.container {
  max-width: 700px;
  margin: 15px auto;
  text-align: center;
  padding: 0 15px;

}

.task-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}



.wrapper {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.input {
 -ms-flex-positive: 3;
     flex-grow: 3;
 font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
 border: none;
 padding: 16px 16px 16px 60px;
 background-color: #FEFEFE;
 -webkit-transition: border .5s;
 -o-transition: border .5s;
 transition: border .5s;
 font-size: 24px;
 font-weight: 300;
 -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
         box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

.task__list {
  list-style: none;
}

.task__item {
  display: -ms-flexbox;
  display: flex;
  font-size: 1.1rem;
  margin: 10px 0;
}

.delete {
  color: #979797;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
}

.task-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
  padding: 16px;
  background-color: #fff;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;

}

.task__item p {
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-align: left;
}

.task-number {
  display: block;
  margin-right: 10px;
}

.success .task-info {
  background-color: #e7e6e6;
  color: #ccc;
}

.task h1 {
  text-align: left;
  margin-bottom: 10px;
  font-weight: 300;
}

.button {
  text-align: center;
  cursor: pointer;
  padding: 10px;
  -ms-flex-preferred-size: 150px;
      flex-basis: 150px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
  background-color: #17d3a1;
  border: none;
  color: #fff;
  border-radius: 5px;
  margin-left: 15px;
  outline: none;
  font-weight: bold;
}

.button-header {
  background-color: #177bd2;
}

.success .button {
  background-color: #e7e6e6;
}

.success p {
  text-decoration: line-through;
  font-style: italic;
}



.footer {
  background-color: #20262E;
  padding: 20px;
  font-size: 15px;
  color: #fff;

}

.author {
  color: #fff;
  text-decoration: none;
}

.hidden-input-text {
  text-align: left;
  color: red;
  font-size: 12px;
  line-height: 1.7;
}


.clear-all {
  text-align: left;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin-top: 10px;
}

.top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}


::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #bfbfbf;
  font-style: italic;
  font-weight: 300;

}
::-moz-placeholder { /* Firefox 19+ */
  color: #bfbfbf;
  font-style: italic;
  font-weight: 300;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #bfbfbf;
  font-style: italic;
  font-weight: 300;
}
:-moz-placeholder { /* Firefox 18- */
  color: #bfbfbf;
  font-style: italic;
  font-weight: 300;
}

@media screen and (max-width: 768px) {
  .task-header {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  /* .task-info {
    padding: 25px 15px;
  } */

  .task__item {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .top {
    display: -ms-flexbox;
    display: flex;
  }



  .button {
    -ms-flex-preferred-size: 50px;
        flex-basis: 50px;
    margin-left: 0;
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }

  .button-header {
    -ms-flex-preferred-size: 59px;
        flex-basis: 59px;
  }

  .input {
    padding: 15px;
    text-align: center;
  }

  .task-info,
  .input {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
}


